import activeModules from '@/pmgc/activeModules'
import ErrPage from '@/components/layouts/404'
// import Index from './index'
import About from './about'
import ContactUs from './contact'
import CBT from '@/pmgc/access/cbt'

let routes = [
  {
    path: '/',
    name: 'Platinum Minds Global Company',
    component: CBT
  },
  {
    path: '/about',
    name: 'About Platinum Minds Global Company',
    component: About
  },
  {
    path: '/contact',
    name: 'PMGC || Contact us',
    component: ContactUs
  },
  {
    path: '*',
    name: 'Error 404',
    component: ErrPage
  }
]

activeModules.forEach(modul => {
  try {
    // get module routes
    let moduleRoutes = require('@/pmgc/' + modul + '/router').default
    // add the routes gotten to the route array
    routes = routes.concat(moduleRoutes)
    // console.log(routes)
  } catch (e) {
    console.log(e)
  }
})

export default routes



