<template>
  <div class="cbtSoftware">
    <div class="cbt text-center center-align">
      <div class="inner">
        <h3>Welcome here</h3>
        <p>Kindly click the below button to add questions</p>
        <button class="btn modal-trigger red" data-target="addQuestionModal">Add Question</button>
        <button class="btn blue">
          <router-link to="/cbtsoftware/practiceTest" class="white-text">Practice Test</router-link>
        </button>
      </div>

      <!-- Modal Structure -->
      <div id="addQuestionModal" class="modal">
        <div class="modal-content">
           <a class="right modal-close x15"><i class="icon ion-close-circled right"></i></a>
          <h4 class="blue-text">Add Question</h4>
          <div class="addQuestion">
            <div class="row">
              <form class="col s12">
                <div class="row">
                  <div class="input-field col s12">
                    <textarea id="textarea1" class="materialize-textarea x15 validate" data-length="120" required v-model="formData.question"></textarea>
                    <label for="textarea1" class="active">Question</label>
                  </div>
                </div>
                <div class="row">
                  <div class="input-field col s10 m6">
                    <input id="optionA" type="text" class="validate" required v-model="option.A">
                    <label for="optionA" class="active">A</label>
                  </div>
                </div>
                <div class="row">
                  <div class="input-field col s10 m6">
                    <input id="optionB" type="text" class="validate" required v-model="option.B">
                    <label for="optionB" class="active">B</label>
                  </div>
                </div>
                <div class="row">
                  <div class="input-field col s10 m6">
                    <input id="optionC" type="text" class="validate" required v-model="option.C">
                    <label for="optionC" class="active">C</label>
                  </div>
                </div>
                <div class="row">
                  <div class="input-field col s10 m6">
                    <input id="optionD" type="text" class="validate" v-model="option.D">
                    <label for="optionD" class="active">D</label>
                  </div>
                </div>
                <div class="input-field col s12 m7"><br/>
                  <select v-model="formData.ans" class="select browser-default">
                    <option value="" disabled selected>Answers</option>
                    <option v-if="option.A" :value="option.A">A</option>
                    <option v-if="option.B" :value="option.B">B</option>
                    <option v-if="option.C" :value="option.C">C</option>
                    <option v-if="option.D" :value="option.D">D</option>
                  </select>
                  <label class="active x2">Select answer<br/></label>
                </div>
              </form>
            </div>
          </div>
        </div>
        <div class="modal-footer text-center center-align v-align" style="text-align: center;">
          <a class="modal-action waves-effect waves-red blue center-align btn" @click="addQuestion">Add</a>
        </div>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import M from 'materialize-css'
import $ from 'jquery'
import AddService from '@/services/addService'
import bcrypt from 'bcryptjs'
export default {
  data () {
    return {
      modalInstance: '',
      formData: {
        question: '',
        options: [],
        ans: ''
      },
      option: {
        A: '',
        B: '',
        C: '',
        D: ''
      }
    }
  },
  mounted () {
    var elem = document.querySelector('#addQuestionModal')
    // eslint-disable-next-line
    var instance = new M.Modal(elem, {})

    this.modalInstance = M.Modal.getInstance(elem)

    // var el = document.querySelector('select')
    // // eslint-disable-next-line
    // var instance = new M.Select(el, {
    //   classes: 's6'
    // })

    $('#textarea1').val('')
    $('#textarea1').trigger('autoresize')
  },
  methods: {
    async addQuestion () {
      let validQData = {}
      if (this.formData.question && this.formData.question.length >= 7) {
        validQData.question = this.formData.question
      } else {
        alert('Please enter a valid question!')
        return
      }

      if (this.option.A && this.option.B && this.option.C && this.option.D) {
        if (this.option.A === this.option.B || this.option.A === this.option.C || this.option.A === this.option.D || this.option.B === this.option.C || this.option.B === this.option.D || this.option.C === this.option.D) {
          alert('Duplicate options is not allowed!')
        } else {
          this.formData.options = [this.option.A, this.option.B, this.option.C, this.option.D]
          validQData.options = this.formData.options
        }
      } else {
        alert('All options are required!')
        return
      }
      if (this.formData.ans) {
        validQData.ans = bcrypt.hashSync(this.formData.ans, 10)
      } else {
        alert('You must choose the answer for this question')
        return
      }
      try {
        const postQuestion = (await AddService.addQuestion(validQData)).data
        console.log({postQuestion})
        alert(postQuestion)
        this.formData.question = ''
        this.formData.options = []
        this.formData.ans = ''
        this.option.A = ''
        this.option.B = ''
        this.option.C = ''
        this.option.D = ''
        this.modalInstance.close()
      } catch (error) {
        if (error.response.data) {
          alert(error.response.data)
        } else {
          console.log(JSON.stringify(error))
          this.formData.question = ''
          this.formData.options = []
          this.formData.ans = ''
          this.option.A = ''
          this.option.B = ''
          this.option.C = ''
          this.option.D = ''
          this.modalInstance.close()
        }
      }
    }
  }
}
</script>

<style scoped>
  input:not([type]):focus:not([readonly]), input[type=text]:not(.browser-default):focus:not([readonly]), input[type=password]:not(.browser-default):focus:not([readonly]), input[type=email]:not(.browser-default):focus:not([readonly]), input[type=url]:not(.browser-default):focus:not([readonly]), input[type=time]:not(.browser-default):focus:not([readonly]), input[type=date]:not(.browser-default):focus:not([readonly]), input[type=datetime]:not(.browser-default):focus:not([readonly]), input[type=datetime-local]:not(.browser-default):focus:not([readonly]), input[type=tel]:not(.browser-default):focus:not([readonly]), input[type=number]:not(.browser-default):focus:not([readonly]), input[type=search]:not(.browser-default):focus:not([readonly]), textarea.materialize-textarea:active:not([readonly]),
   input:not([type]):active:not([readonly]), input[type=text]:not(.browser-default):active:not([readonly]), input[type=password]:not(.browser-default):active:not([readonly]), input[type=email]:not(.browser-default):active:not([readonly]), input[type=url]:not(.browser-default):active:not([readonly]), input[type=time]:not(.browser-default):active:not([readonly]), input[type=date]:not(.browser-default):active:not([readonly]), input[type=datetime]:not(.browser-default):active:not([readonly]), input[type=datetime-local]:not(.browser-default):active:not([readonly]), input[type=tel]:not(.browser-default):active:not([readonly]), input[type=number]:not(.browser-default):active:not([readonly]), input[type=search]:not(.browser-default):active:not([readonly]), textarea.materialize-textarea:focus:not([readonly]) {
    border-bottom: 1px solid #2691f3;
    box-shadow: 0 1px 0 0 #2691f3;
  }
  input:not([type]):focus:not([readonly])+label, input[type=text]:not(.browser-default):focus:not([readonly])+label, input[type=password]:not(.browser-default):focus:not([readonly])+label, input[type=email]:not(.browser-default):focus:not([readonly])+label, input[type=url]:not(.browser-default):focus:not([readonly])+label, input[type=time]:not(.browser-default):focus:not([readonly])+label, input[type=date]:not(.browser-default):focus:not([readonly])+label, input[type=datetime]:not(.browser-default):focus:not([readonly])+label, input[type=datetime-local]:not(.browser-default):focus:not([readonly])+label, input[type=tel]:not(.browser-default):focus:not([readonly])+label, input[type=number]:not(.browser-default):focus:not([readonly])+label, input[type=search]:not(.browser-default):focus:not([readonly])+label, textarea.materialize-textarea:focus:not([readonly])+label {
    color: #2691f3;
  }
  #addQuestionModal > div.modal-content > div > div > form > div.input-field.col.s12.m6 {
    margin-left: -3%;
  }
  div.cbtSoftware .input-field.col label.active {
    font-size: 1.5rem;
  }
  div.cbtSoftware {
    position: fixed;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-size: 100% 100%;
    z-index: 1000;
    display: -webkit-flex;
    padding: 0 !important;
    display: -ms-flexbox;
    display: flex;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
  }
</style>



