<template>
  <div class="navbar">
    <nav class="blue-grey white-text darken-4">
      <div class="nav-wrapper">
         <a class="brand-logo animated bounceIn left">
           <img src="../../assets/logowhite.png" alt="PMGC" width="50px" height="62px">
         </a>
        <ul class="right hide-on-small-only">
          <li>
            <router-link to="/" class="btn blue">Home</router-link>
          </li>
          <li>
            <router-link to="/achievements" class="btn blue ">Achievements</router-link>
          </li>
          <li>
            <router-link to="/news" class="btn blue">News</router-link>
          </li>
          <li>
            <router-link to="/gallery" class="btn blue">Gallery</router-link>
          </li>
          <li>
            <router-link to="/about" class="btn blue">About</router-link>
          </li>
          <li>
            <router-link to="/contact" class="btn blue">Contact</router-link>
          </li>
        </ul>
      </div>
    </nav>
  </div>
</template>

<script>
export default {}
</script>

<style>
  div.navbar nav a{
    color: #fff !important;
    /* line-height: 39px; */
  }

  /* div.navbar nav li a:hover, div.navbar nav ul li a:focus, div.navbar nav ul li a:visited, div.navbar nav ul li a:active {
  } */
  div.navbar > nav > div > ul > li a:hover, div.navbar > nav > div > ul > li a:focus, div.navbar > nav > div > ul > li a:active, div.navbar > nav > div > ul > li a:visited {
    border-top: 2px solid #fff;
    border-bottom: 2px solid #fff;
  }
  div.nav-wrapper {
    padding-left: 2.9rem;
  }
  div.navbar > nav > div > ul > li > a.btn, #app > div > div.navbar > nav > div li > a.btn {
    font-size: 0.7rem;
    height: 30px;
    line-height: 30px;
    padding: 0 12px;
  }
</style>



