<template>
  <div class="practiceTest">
    <div class="row">
      <div class="container">
        <div class="card-panel white grey-text text-darken-4">
          <div class="pmgcPractice">
            <h3 class="blue white-text text-center center-align" style="padding: 0.5em;">Computer Based Testing</h3>
            <p class="notice">
              <a class="right"><i class="icon ion-globe x3 blue amber-text"></i></a>
              <!-- <button class="btn blue waves-effect right">Submit</button> -->
            </p>
            <hr style="margin-top: 20px;">
            <div class="testingGround" id="testingGround">
              <span class="right x1">Question {{questionNo}} of {{questions.length}}</span><br/>
                <h5 id="eachQuestion">
                  {{questionNo}}.&nbsp;
                  {{question}}
                </h5>
              <div class="options" style="margin-left: 6%;">
                <p v-for="(option, index) in options" :key="index">
                  <label>
                    <input class="with-gap" name="choices" type="radio" :value="option"/>
                    <span>{{option}}</span>
                  </label>
                </p>
                  <a class="btn blue waves-effect">Previous</a>
                  <a class="btn blue right waves-effect" @click="nextQuestion">Next</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import fetchService from '@/services/fetchService'
import $ from 'jquery'
import bcrypt from 'bcryptjs'
export default {
  data () {
    return {
      choices: document.getElementsByName('choices'),
      pos: 0,
      choice: '',
      questionNo: 0,
      correctlyAnswered: 0,
      options: [],
      question: '',
      ans: '',
      questions: []
    }
  },
  async mounted () {
    try {
      const allQuestions = (await fetchService.fetchQuestions()).data
      this.questions = allQuestions
      this.questions.forEach(q => {
        q.questionOptions = this.reshuffleData(q.questionOptions)
      })
      console.log({questions: this.questions})
    } catch (error) {
      if (error) {
        console.log({error})
      }
    }
    if (this.questions && this.questions.length > 0) {
      this.renderQuestions()
    } else {
      alert('Unable to fetch questions')
      this.$router.push(`/cbtsoftware`)
    }
  },
  methods: {
    renderQuestions () {
      // coding the major questions and ans
      console.log({correctlyAnswered: this.correctlyAnswered})
      if (this.pos >= this.questions.length) {
        alert(`Practice complete. You scored ${this.correctlyAnswered} out of ${this.questions.length}`)
        let completeText = $('#testingGround')
        completeText.addClass('text-center center-align big')
        completeText.text('Test completed')
        return false
      }
      this.questionNo = this.pos + 1
      this.question = this.questions[this.pos].questionText
      this.options = this.questions[this.pos].questionOptions
      this.ans = this.questions[this.pos].answer
    },
    nextQuestion () {
      this.choices.forEach(choice => {
        if (choice.checked) {
          this.choice = choice.value
        }
      })
      if (bcrypt.compareSync(this.choice, this.ans)) {
        this.correctlyAnswered++
      }
      this.pos++
      this.choices.forEach(ch => {
        if (ch.checked) {
          ch.checked = false
          this.choice = ''
        }
      })
      this.renderQuestions()
    },
    reshuffleData (array) {
      var currentIndex = array.length
      var temporaryValue
      var randomIndex

      // While there remain elements to shuffle...
      while (currentIndex !== 0) {
        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex)
        currentIndex -= 1

        // And swap it with the current element.
        temporaryValue = array[currentIndex]
        array[currentIndex] = array[randomIndex]
        array[randomIndex] = temporaryValue
      }
      return array
    }
  }
}
</script>

<style scoped>
  .big {
    font-size: 2.5rem !important;
  }
  [type="radio"]:checked+span:after, [type="radio"].with-gap:checked+span:before, [type="radio"].with-gap:checked+span:after {
    border: 2px solid #2691f3 !important;
  }
  [type="radio"]:checked+span:after, [type="radio"].with-gap:checked+span:after {
    background-color: #2691f3 !important;
}
</style>



