import Vue from 'vue'
import Router from 'vue-router'
import Routes from '@/pmgc/router'

Vue.use(Router)

const router = new Router({
  routes: Routes,
  mode: 'history'
})

// eslint-disable-next-line
let updateStore = (key, value) => {
  let storeData = {}
  if (window.localStorage.getItem('PMGC-default')) {
    storeData = JSON.parse(JSON.parse(window.localStorage.getItem('PMGC-default')).value)
  }
  storeData[key] = value
  localStorage.setItem('PMGC-default', JSON.stringify({value: JSON.stringify(storeData)}))
}

// eslint-disable-next-line
let fetchStoreData = key => {
  if (window.localStorage.getItem('PMGC-default') && window.localStorage.getItem('PMGC-default') !== null) {
    return JSON.parse(JSON.parse(window.localStorage.getItem('PMGC-default')).value)[key]
  } else {
    return null
  }
}

export default router



