import Vue from 'vue'
import Vuex from 'vuex'
import createPersist from 'vuex-localstorage'

Vue.use(Vuex)

export default new Vuex.Store({
  strict: true,
  state: {
    admiin: [],
    currentGallery: '',
    currentMember: ''
  },
  mutations: {},
  plugins: [createPersist({
    namespace: 'PMGC',
    expires: 1 * 60 * 60 * 1e3
  })]
})



